import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './Scoresheet.css';

class Scoresheet2 extends Component {
  render() {
    const {
      allTeams, data,
    } = this.props;

    return data ? (
      <div>
        <p>
          {data.byStage[0][0].riders[0].name}
          {data.byStage[0][0].riders[0].scores.map(score => (
            <p>
              {score.name}
              :
              {score.points}
            </p>
          ))}
        </p>
        <div>
          {data.byTeam.map(team => (
            <p>
              {team.name}
              :
              {team.score}
            </p>
          ))}
        </div>
      </div>
    ) : null;
  }
}

export default Scoresheet2;



